
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

import * as base from '@/api/base'
import { checkLoginToLogin } from '@/api/login'
export default {
name: 'turn-the-card',
components: {
XHeader,
XInput,
Cell,
Group,
XButton,
AlertModule,
Alert
},
methods: {
trunCard () {
if (this.isSelf) {
return this.trunCardToSelf()
}
if (this.turnCount < 1) {
return this.$vux.toast.text('请输入大于0的转卡数')
}
base.fetch('/api/v1/agentcards', {
childrenid: this.$route.params.id,
cardnum: this.turnCount
}, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
if (data.code !== 0) {
return this.$vux.toast.text(msg, 'middle')
}
base.setUserInfo('user_stock', parseInt(base.getSingleUserInfo('user_stock')) - parseInt(this.turnCount))
this.$vux.alert.show({
title: '温馨提示',
content: msg,
onHide: () => {
this.$router.go(-1)
}
})
}, error => {
console.log(error)
})
},
trunCardToSelf () {
base.fetch('/api/v1/agentcards/stocktocard', {
cardnum: this.turnCount
}, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}
base.setUserInfo('user_stock', parseInt(base.getSingleUserInfo('user_stock')) - parseInt(this.turnCount))
base.setUserInfo('user_card', parseInt(base.getSingleUserInfo('user_card')) + parseInt(this.turnCount))
this.$vux.alert.show({
title: '温馨提示',
content: msg,
onHide: () => {
this.$router.go(-1)
}
})
}, error => {
console.log(error)
})
}
},
data () {
return {
show: false,
params: {},
placeHolderText: '',
turnCount: '',
isSelf: false,
myCard: base.getSingleUserInfo('user_card'),
myStock: base.getSingleUserInfo('user_stock')
}
},
created () {
checkLoginToLogin(this)
let params = this.$route.fullPath.split('?')[1]
this.params = querystring.parse(params)
this.isSelf = this.$route.params.id.toString() === base.getSingleUserInfo('user_' + base.getSingleUserInfo('user_identity').current + 'ShortId').toString()
base.fetch('/api/v1/agents/userinfo', null, response => {
let data = response.data
this.placeHolderText = '最多' + data.coin.stock + '张房卡'
}, error => {
console.log(error)
}, 'GET')
}
}



